import React, { useState } from 'react';
import NeuralNetworkVisualization from './components/NeuralNetworkFunctionApproximator';
import VAEVisualizer from './components/VAEVisualizer';
import GradientDescent from './components/GradientDescentAtSaddlePoint';
import EpochAndBatch from './components/EpochAndBatchVisualization';
import EncoderDecoder from './components/EncoderDecoderVisualizer';
import HessianEigenvalue from './components/HessianAndEigenvalue';
import LearningRate from './components/LearningRate';
import Normalization from './components/Normalization';
import AdaptiveLearning from './components/AdaptiveLearningComparison';
import PositionEmbedding from './components/PositionEmbedding';
import BetaVAE from './components/BetaVAE';
import ActivationFunctions from './components/ActivationFunctions';
import LossFunctions from './components/LossFunctions';
import RegularizationNormalization from './components/RegularizationNormalization';
import Eigenvalue from './components/Eigenvalue';
import InvertibleMatrix from './components/InvertibleMatrix';

function App() {
  const [activeComponent, setActiveComponent] = useState('neural');
  
  // Button data array
  const buttons = [
    { id: 'neural', label: 'Function Approximation' },
    { id: 'vae', label: 'Variational Autoencoder' },
    { id: 'gd', label: 'Saddle Points' },
    { id: 'eb', label: 'Epochs & Batches' },
    { id: 'ed', label: 'Encoder-Decoder Models' },
    { id: 'hessian', label: 'Hessian & Eigenvalues' },
    { id: 'learningrate', label: 'Learning Rates' },
    { id: 'normalization', label: 'Normalization Techniques' },
    { id: 'adaptive', label: 'Adaptive Learning Rate Comparison' },
    { id: 'position', label: 'Position Embedding' },
    { id: 'betavae', label: 'Beta VAE' },
    { id: 'activation', label: 'Activation functions' },
    { id: 'loss', label: 'Loss functions' },
    { id: 'rn', label: 'Regularization & Normalization' },
    { id: 'eigenvalue', label: 'Eigenvalue & Transform' },
    { id: 'invertible', label: 'Invertible Matrix' }
  ];
  
  // Button style generator function
  const getButtonStyle = (id) => ({
    padding: '8px 16px',
    border: 'none',
    borderRadius: '4px',
    backgroundColor: activeComponent === id ? '#007bff' : '#f0f0f0',
    color: activeComponent === id ? 'white' : 'black',
    cursor: 'pointer',
    fontWeight: activeComponent === id ? 'bold' : 'normal',
    transition: 'background-color 0.2s'
  });
  
  // Component mapping object
  const componentMap = {
    neural: <NeuralNetworkVisualization />,
    vae: <VAEVisualizer />,
    gd: <GradientDescent />,
    eb: <EpochAndBatch />,
    ed: <EncoderDecoder />,
    hessian: <HessianEigenvalue />,
    learningrate: <LearningRate />,
    normalization: <Normalization />,
    adaptive: <AdaptiveLearning />,
    position: <PositionEmbedding />,
    betavae: <BetaVAE />,
    activation: <ActivationFunctions />,
    loss: <LossFunctions />,
    rn: <RegularizationNormalization />,
    eigenvalue: <Eigenvalue />,
    invertible: <InvertibleMatrix />
  };
  
  return (
    <div className="App">
      <header style={{ 
        padding: '12px 20px', 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center', 
        borderBottom: '1px solid #e0e0e0' 
      }}>
        <div style={{ fontSize: '16px', fontWeight: 'bold' }}>Machine Learning Algorithm Visualizer</div>
        <div className="tabs" style={{ 
          display: 'flex', 
          flexWrap: 'wrap',  
          gap: '10px',       
          justifyContent: 'flex-start'
        }}>
          {buttons.map(button => (
            <button 
              key={button.id}
              onClick={() => setActiveComponent(button.id)}
              style={getButtonStyle(button.id)}
            >
              {button.label}
            </button>
          ))}
        </div>
      </header>
      
      <div style={{ padding: '20px' }}>
        {componentMap[activeComponent]}
      </div>
    </div>
  );
}

export default App;
